@import url(http://fonts.googleapis.com/earlyaccess/nanumgothic.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.card {
    display: inline-block;
    width: 18%;
    margin: 1.2rem;
    padding: 0 1rem;
    border-radius: 20px;
}

.cardName {
    font-weight: normal;
    font-size: calc(10px + 3vmin);
    text-align: left;
    margin: 1.5rem 0 0.5rem 1rem;
    padding: 0;
}

.cardId {
    font-weight: normal;
    font-size: calc(10px + .5vmin);
    text-align: right;
}

.cardTier {
    display: block;
    font-weight: normal;
    font-size: calc(10px + 1vmin);
    text-align: right;
    margin: 0.5rem 1rem;
    padding: 0;
    color: #FFFFFF;
}

.cardSolved {
    display: block;
    font-weight: normal;
    font-size: calc(10px + 1vmin);
    text-align: right;
    margin: 0.5rem 1rem;
    padding: 0;
}

.cardClasses {
    display: block;
    font-weight: normal;
    font-size: calc(10px + 1vmin);
    text-align: right;
    margin: 0.5rem 1rem 1rem 0;
    padding: 0;
}

.bg-bronze {
    background: linear-gradient( to right, #F09661 , #AD5600 );
}

.bg-silver {
    background: linear-gradient( to right, #7F7CC9 , #435F7A );
}

.bg-gold {
    background: linear-gradient( to right, #F3CA5A , #EDA010 );
}

.bg-platinum {
    background: linear-gradient( to right, #86E5CF , #27E2A4 );
}

body {
  font-family: "Nanum Gothic", sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: black;
}

.App-header-dark {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-weight: bold;
  color: black;
}

.App-box {
  width: 50%;
  height: 70%;
  background-color: rgb(255 255 255 / 50%);
  padding: 2rem;
}

.Card-box {
  width: 100%;
  height: 70%;
  background-color: rgb(255 255 255 / 50%);
  padding: 2rem;
}

.App-body {
  font-weight: normal;
  font-size: calc(10px + 1.3vmin);
  margin: 3px;
}

.small-body {
  font-weight: normal;
  font-size: calc(10px + 0.6vmin);
  margin: 3px;
}

.White-title {
  color: #777777;
  font-weight: bold;
  font-size: calc(10px + 6vmin);
}

.Black-title {
  color: #000000;
  font-weight: bold;
  font-size: calc(10px + 6vmin);
}

.left-align {
  text-align: left;
}

.bg-solved {
  background-image: url("https://user-images.githubusercontent.com/28296575/130800829-79965293-3511-4ada-bda5-0ad5d054cff4.png");
  background-repeat: no-repeat;
  background-position: right center;
}

.bg-company {
  background-image: url("https://user-images.githubusercontent.com/28296575/131001985-5ef33510-1723-40f9-8288-f34d820cdaad.png");
  background-repeat: no-repeat;
  background-position: center;
}

.bg-ocean {
  background-image: url(/2021SummerAlgorithm/static/media/background.dc50fac8.jpg);
}


h5 {
  margin: 0;
}

img {
  margin-top: 1rem;
}

.fade-in {
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

